.App {
    text-align : center;
    width : 100%;
}

.buttons > div {
   display : flex ;
   flex-direction: row;
   justify-content: space-around;
}

.buttons > div > * {
    text-decoration: none;
}


@media (max-width : 600px ){
    .buttons {
        display : none 
    }
}


.buttons > * > * {
    height : 100%;
}

.Logo {
    line-height: 40px ;
    color : orange ;
    font-size : 40px ;
}

.container {
    height : 20px; 
    background-color : green;
}

.nav {
    background-color : #7e57c2;
    color :#9e9e9e;
    position : fixed ;
    top : 0;
    left : 0;
    z-index : 5;
}

@media (min-width : 600px){
    .menu {
        display :none;
    }
}

.menu > * {
    float :left
}