.side {
    position : fixed;
    top : 0;
    left : 0 ;
    width : 40%;
    height : 100%;
    background-color : #f5f5f5;
    z-index : 100;
    display : flex ;
    flex-direction: column;
}

.side > * {
    display: block;
    margin : 15px ;
    text-align : center;
    text-decoration : none ;
    
}